import { Button, ButtonGroup, FormControl,  ListSubheader, Select, InputLabel, MenuItem, Menu, Modal, Box, Grid, Tabs, Tab} from "@mui/material";
import { styled } from "@mui/material/styles";
import { DemoContainer } from '@mui/x-date-pickers/internals/demo';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import axios from "axios";
import React ,{ useState, useEffect } from "react";
import dayjs from "dayjs";
import TabPanel from "./TabPanel";



function Ticketinfoform() {

  // const API_KEY = process.env.REACT_APP_PUBLICAPI_KEY; // api_key .ENV  . 
  const [tripType, setTripType] = useState("one-way"); // 
  const [transportType, setTransportType] = useState("train"); //        
  const [selectedType, setSelectedType] = useState(''); //        
  const [datevalue, setdatevalue] = useState(dayjs()); //  
  const [party, setParty] = useState(''); // 
  const [anchorEL, setanchorEL] = useState(null); //   
  const [Province, setProvince] = useState(null); //      or     .
  const [selectLocation, setSelectLocation] = useState([]);
  const [ktxStationsByCity, setKtxStationsByCity] = useState({}); //    
  const [terminalData, setTerminalData] = useState([]); // 
  const [Modalopen, setModalOpen] = React.useState(false); //  ,  
  const [selectedTab, setSelectedTab] = useState(0); 
  const handleModalOpen = () => setModalOpen(true);
  const handleModalClose = () => setModalOpen(false); // Modal --- 
// 
  const handleParty = (event) => {
    setParty(event.target.value);
  };

  const handleSubmit = (event) => {
    
    console.log("Selected Departure:", Province);
    //  , , ,       .

    // :    
    console.log("Selected Departure:", Province[selectLocation]);
    console.log("Selected Destination:", Province[selectLocation]);
    console.log("Selected Date:", datevalue.format("YYYY-MM-DD"));
    console.log("Selected Party Size:", party);

    // TODO:      
  };
 
  // const handleCitySelect = (cityCode) => {
  //   fetchcitytrainSttn(cityCode);
  // };


  const handleTripTypeChange = (type) => {
    setTripType(type);
    setSelectedType(selectedType);
  };

  const handleTransportTypeChange = (type) => {
    setTransportType(type);
    setSelectedType(type === 'train' ? '' : '');
  
}

const handlebuttonClick = (event) => {
  setanchorEL(event.currentTarget);
};

const handlebuttonClose = () => {
  setanchorEL(null);
};

function Subheader(props) {
  return React.createElement(ListSubheader, props);
}
 Subheader.muiSkipListHighlight = true;
  

 useEffect(() => {
  const fetchStationsForCities = async () => {
    const tempStationsByCity = {};
    for (const city of selectLocation) {
      const response = await axios.get(`/publicApi/getCitySttnList?cityCode=${city.cityCode}`);
      const responseData = response.data;
      tempStationsByCity[city.cityCode] = responseData; // responseData   
    }
    setKtxStationsByCity(tempStationsByCity);
  };
  fetchStationsForCities();
}, [selectLocation]);


useEffect(() => {
  const fetchcityCodeList = async () => {
    try {
      const response = await axios.get('/publicApi/getCityCodeList');
      const responseData = response.data;
      setSelectLocation(responseData);
    } catch (error) {
      console.error("API  ()", error);
    }
  };
  fetchcityCodeList();
}, []);


//   
useEffect(() => {
  //    API    
  fetchBusTerminalData();
}, []);

// API   
const fetchBusTerminalData = () => {
  // API  URL
  const apiUrl = '/publicApi/getBusList'; // API URL   .

  // Axios  API 
  axios.get(apiUrl)
    .then((response) => {
      // API     
      setTerminalData(response.data);
    })
    .catch((error) => {
      console.error('API  :', error);
    });
};

const busTerminals = terminalData.map((terminal) => ({
  id: terminal.terminalId,
  regionKey: terminal.regionKey,
  name: terminal.terminalName,
}));


const handleTabChange = (event, newValue) => {
  setSelectedTab(newValue);
};



// Modal style

const Modalstyle = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};



 return(

 <div className="ticket-info-form">
<h2> </h2>

<div className= "form-state-button-groups">
<ButtonGroup variant="outlined" aria-label = "outlined button group">
<Button onClick={() => handleTripTypeChange('one-way')}></Button>
<Button onClick={() => handleTripTypeChange('round-trip')}></Button>
<Button id = "Dropdonw-menu" aria-haspopup = "true" onClick = {handlebuttonClick}  >
  
</Button>
<Menu
        id="dropdown-menu"
        anchorEl={anchorEL}
        open={Boolean(anchorEL)}
        onClose={handlebuttonClose}
      >
        <MenuItem onClick={() => { handlebuttonClose(); handleTransportTypeChange('train'); }}></MenuItem>
        <MenuItem onClick={() => { handlebuttonClose(); handleTransportTypeChange('bus'); }}></MenuItem>
      </Menu>
</ButtonGroup>
</div>


{transportType === 'train' && tripType === 'one-way' && ( 

<form onSubmit = {handleSubmit} className = "form-ticketinfo-form">
  <FormControl sx={{ m: 1, minWidth: 120 }}>
        <InputLabel htmlFor="grouped-select"></InputLabel>
        <Select defaultValue="" id="grouped-select" label="Grouping">
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          {selectLocation.map(city => [
            <ListSubheader key={city.cityCode}>{city.cityName}</ListSubheader>,
            ...(ktxStationsByCity[city.cityCode] || []).map(station => (
              <MenuItem key={station.nodeId} value={station.nodeId}>
                {station.nodeName}
              </MenuItem>
            ))
          ])}
      </Select>
    </FormControl>

<FormControl sx={{ m: 1, minWidth: 120 }}>
        <InputLabel htmlFor="grouped-select"></InputLabel>
        <Select defaultValue="" id="grouped-select" label="Grouping">
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          {selectLocation.map(city => [
            <ListSubheader key={city.cityCode}>{city.cityName}</ListSubheader>,
            ...(ktxStationsByCity[city.cityCode] || []).map(station => (
              <MenuItem key={station.nodeId} value={station.nodeId}>
                {station.nodeName}
              </MenuItem>
            ))
          ])}
        </Select>
      </FormControl>

<FormControl sx={{ m: 1, minWidth: 120 }}>
       <LocalizationProvider dateAdapter={AdapterDayjs}>
        <DemoContainer components={['DatePicker']}>
        <DatePicker
          label=""
          value={datevalue}
          onChange={(newdatevalue) => setdatevalue(newdatevalue)}
          />
        </DemoContainer>
       </LocalizationProvider>
        </FormControl>

        <FormControl sx={{ m: 1, minWidth: 120 }}>
        <InputLabel id="demo-simple-select-helper-label"></InputLabel>
        <Select
          labelId="demo-simple-select-helper-label"
          id="demo-simple-select-helper"
          value={party}
          label="Age"
          onChange={handleParty}
          >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          {[...Array(10)].map((_, index) => (
      <MenuItem key={index + 1} value={index + 1}>
        {index + 1}
      </MenuItem>
    ))}
        </Select>
        </FormControl>
        <Button type="sumbit"></Button>
          </form>
            )}




{transportType === 'train'&& tripType === 'round-trip' && (


<form onSubmit = {handleSubmit} className = "form-ticketinfo-form">
<div style={{ display: 'flex', gap: '16px' }}>
  <FormControl sx={{ m: 1, minWidth: 120 }}>
        <InputLabel htmlFor="grouped-select"></InputLabel>
        <Select defaultValue="" id="grouped-select" label="Grouping">
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          {selectLocation.map(city => [
            <ListSubheader key={city.cityCode}>{city.cityName}</ListSubheader>,
            ...(ktxStationsByCity[city.cityCode] || []).map(station => (
              <MenuItem key={station.nodeId} value={station.nodeId}>
                {station.nodeName}
              </MenuItem>
            ))
          ])}
        </Select>
      </FormControl>
<FormControl sx={{ m: 1, minWidth: 120 }}>
        <InputLabel htmlFor="grouped-select"></InputLabel>
        <Select defaultValue="" id="grouped-select" label="Grouping">
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          {selectLocation.map(city => [
            <ListSubheader key={city.cityCode}>{city.cityName}</ListSubheader>,
            ...(ktxStationsByCity[city.cityCode] || []).map(station => (
              <MenuItem key={station.nodeId} value={station.nodeId}>
                {station.nodeName}
              </MenuItem>
            ))
          ])}
        </Select>
      </FormControl>

<FormControl sx={{ m: 1, minWidth: 120 }}>
       <LocalizationProvider dateAdapter={AdapterDayjs}>
        <DemoContainer components={['DatePicker']}>
        <DatePicker
          label=""
          value={datevalue}
          onChange={(newdatevalue) => setdatevalue(newdatevalue)}
          />

        </DemoContainer>

       </LocalizationProvider>
        </FormControl>

        <FormControl sx={{ m: 1, minWidth: 120 }}>
        <InputLabel id="demo-simple-select-helper-label"></InputLabel>
        <Select
          labelId="demo-simple-select-helper-label"
          id="demo-simple-select-helper"
          value={party}
          label="Age"
          onChange={handleParty}
          >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
      {[...Array(10)].map((_, index) => (
      <MenuItem key={index + 1} value={index + 1}>
        {index + 1}
      </MenuItem>
    ))}
        </Select>
        </FormControl>
            </div>
        <div style={{ display: 'flex', gap: '16px', marginTop: '16px' }}>
        <FormControl sx={{ m: 1, minWidth: 120 }}>
        <InputLabel htmlFor="grouped-select"></InputLabel>
        <Select defaultValue="" id="grouped-select" label="Grouping">
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          {selectLocation.map(city => [
            <ListSubheader key={city.cityCode}>{city.cityName}</ListSubheader>,
            ...(ktxStationsByCity[city.cityCode] || []).map(station => (
              <MenuItem key={station.nodeId} value={station.nodeId}>
                {station.nodeName}
              </MenuItem>
            ))
          ])}
        </Select>
      </FormControl>

<FormControl sx={{ m: 1, minWidth: 120 }}>
        <InputLabel htmlFor="grouped-select"></InputLabel>
        <Select defaultValue="" id="grouped-select" label="Grouping">
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          {selectLocation.map(city => [
            <ListSubheader key={city.cityCode}>{city.cityName}</ListSubheader>,
            ...(ktxStationsByCity[city.cityCode] || []).map(station => (
              <MenuItem key={station.nodeId} value={station.nodeId}>
                {station.nodeName}
              </MenuItem>
            ))
          ])}
        </Select>
      </FormControl>

<FormControl sx={{ m: 1, minWidth: 120 }}>
       <LocalizationProvider dateAdapter={AdapterDayjs}>
        <DemoContainer components={['DatePicker']}>
        <DatePicker
          label=""
          value={datevalue}
          onChange={(newdatevalue) => setdatevalue(newdatevalue)}
          />

        </DemoContainer>

       </LocalizationProvider>
        </FormControl>

        <FormControl sx={{ m: 1, minWidth: 120 }}>
        <InputLabel id="demo-simple-select-helper-label"></InputLabel>
        <Select
          labelId="demo-simple-select-helper-label"
          id="demo-simple-select-helper"
          value={party}
          label="Age"
          onChange={handleParty}
          >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          {[...Array(10)].map((_, index) => (
      <MenuItem key={index + 1} value={index + 1}>
        {index + 1}
      </MenuItem>
    ))}
        </Select>
        </FormControl>
        </div>
        <Button type="sumbit"></Button>
    {/* </div> */}
          </form>
            )}

{transportType === 'bus' && tripType === 'one-way' && (
        <>
    <Button onClick={handleModalOpen}>Open modal</Button>
          <Modal
            open={Modalopen}
            onClose={handleModalClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
          >
            <Box sx={{ flexGrow: 1, bgcolor: 'background.paper', display: 'flex', height: 500 }}>
              <Tabs
                orientation="vertical"
                variant="scrollable"
                value={selectedTab}
                onChange={handleTabChange}
                aria-label="Vertical tabs example"
              >
                {busTerminals.map((terminal, index) => (
                  <Tab label={terminal.regionKey} key={terminal.terminalId} value={terminal.terminalId} />
                ))}
              </Tabs>
              {busTerminals.map((terminal, index) => (
                <TabPanel key={terminal.regionKey} value={selectedTab} index={terminal.terminalId}>
                  {/* Display the bus terminal information here */}
                  <p>{terminal.terminalName}</p>
                  {/* Add more details if needed */}
                </TabPanel>
              ))}
            </Box>
          </Modal>
        </>
      )}


</div>     



 );
}





export default Ticketinfoform;